/** @jsx jsx */
import { Flex, Themed, jsx, Divider } from 'theme-ui'
import * as React from "react"
import { graphql, Link, useStaticQuery } from "gatsby"
import { getImage, StaticImage } from "gatsby-plugin-image"
import BackgroundImage from 'gatsby-background-image'
import { convertToBgImage } from "gbimage-bridge"

import Layout from "../components/layout"
import Seo from "../components/seo"

import { PoolInfo } from "../components/pool-info"
  export function Hero() {
  return (
    <div>
      <StaticImage
        sx={{
          position: 'absolute',
          top: 0,
          right: 0,
          width: ['90%', '80%'],
          height: '100vh',
        }}
        layout="fullWidth"
        alt=""
        src={
          "../images/bg.png"
        }
        formats={["auto", "webp", "avif"]}
      />
    <div sx = {{ mt: ['2em', '3em', '5em'], mx: ['2em', '3em', '5em'] }}>
      <Logo />
      <div sx={{
            m: ['1em', '1em', '3em']
          }} >
        <PoolInfo />
    </div>
    </div>
    </div>
  )
}

const Logo = () => {
  return (
    <StaticImage
    sx = {{width: [ '4em', '6em', '10em' ], m: '3em'}}
      src="../images/logo.png"
      formats={["auto", "webp", "avif"]}
    />
  )
}

const IndexPage = () => {
  const { background } = useStaticQuery(
    graphql`
     query {
       background: file(relativePath: { eq: "bg.png" }) {
         childImageSharp {
            gatsbyImageData(
             width: 600
             placeholder: BLURRED
             formats: [AUTO, WEBP, AVIF]
           )

         }
       }
     }
       `)
  // workaround before gatsby-background-image updates
  // https://github.com/gatsbyjs/gatsby/issues/2470 <- no bg-image
  const imageData = convertToBgImage(getImage(background))
  return (
    <Layout>
      <Seo title="Home" />
      <Hero />
    </Layout>
  )
}

export default IndexPage
